/* eslint-disable react/jsx-curly-brace-presence */
import React from 'react';
import { Helmet } from 'react-helmet';
import { withPrefix } from 'gatsby';

import Layout from '@/components/Layout';
import Hero from '@/components/Layout/sections/Hero';
import Services from '@/components/Layout/sections/Services';

export default function Home({ location }) {
  return (
    <Layout>
      <Helmet>
        <meta charSet="utf-8" />
        <title>igencorp | create company online</title>
        <link rel="canonical" href="https://igencorp.co" />
        <script
          src={withPrefix(`main.js`)}
          async={false}
          type="text/javascript"
        />
        <script>{`
          var interval = setInterval(() => {
            var globeDiv = document.getElementById("globe");
            if (window.Globe !== undefined && globeDiv !== undefined) {
              var g = new window.Globe(globeDiv.clientWidth, globeDiv.clientHeight, "globe-canvas");
              window.globeRenderer = g.init();
              g.initGlobe();
              g.animate()
              globeDiv.appendChild(window.globeRenderer.domElement);
              window.addEventListener("resize", () => {
                g.setAspectSize(globeDiv.clientWidth, globeDiv.clientHeight);
              }, false);
              clearInterval(interval);
            }
          }, 500);
        `}</script>
      </Helmet>
      <Hero />
      <Services location={location} />
    </Layout>
  );
}
