import React from 'react';
import { Link } from 'gatsby';

interface ServiceItemProps {
  title: string;
  description: string;
  image: string;
  imageDivClassOrder: string;
}

interface ServiceItemImageProps {
  image: string;
  title: string;
}

function ServiceItemImage({ image, title }: ServiceItemImageProps) {
  return (
    <img
      className="border-solid border rounded-lg border-sky-500"
      src={image}
      alt={title}
    />
  );
}

export default function ServiceItem({
  title,
  description,
  image,
  imageDivClassOrder,
}: ServiceItemProps) {
  return (
    <div className="grid md:grid-cols-2 sm:grid-cols-1 gap-4 p-4">
      <div className="flex flex-col justify-center">
        <h1 className="m-4 text-4xl">{title}</h1>
        <p className="m-4">{description}</p>
        <Link
          className="bg-blue-500 md:w-1/4 sm:w-full text-center hover:bg-blue-700 text-white font-bold py-2 px-4 m-4 rounded-xl transition"
          to="/start-company/"
          state={{ title }}
        >
          See it in action
        </Link>
      </div>
      <div className={imageDivClassOrder}>
        <ServiceItemImage image={image} title={title} />
      </div>
    </div>
  );
}
